@import url(https://fonts.googleapis.com/css?family=Lato:400,900,900i&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

a {
    color: black;
}
.navbar {
    margin-bottom: 20px;
}

.navbar a {
    color: black;
}

.nav-item {
    display: flex;
}
.figure-img {
    margin-right: 40px;
}
*{
    font-family: 'Lato';
    font-weight: 600;

}
